export default {
	data() {
		return {
			swelfare:{
				isBagEating: 2,
				encase: 2,
				isTrafficSubsidy: 2,
				royalty: 2,
				idFreeEdu: 2,
			},
			speriod:"",
			speriodName:"",
			money:"",
			sunit:"",
			jobData:{},				
			welfares:[
				{
					id:"0",
					name:"包吃",
					isSelect:false
				},
				{
					id:"1",
					name:"包住",
					isSelect:false
				},
				{
					id:"2",
					name:"交通补贴",
					isSelect:false
				},
				{
					id:"3",
					name:"有提成",
					isSelect:false
				},
				{
					id:"4",
					name:"免费培训",
					isSelect:false
				}
			],
			units:[
				{
					id:"0",
					name:"天",
					isSelect:false
				},
				{
					id:"1",
					name:"小时",
					isSelect:false
				},
				{
					id:"2",
					name:"月",
					isSelect:false
				},
				{
					id:"3",
					name:"次",
					isSelect:false
				},
				{
					id:"4",
					name:"单",
					isSelect:false
				}
			],
			periods:[
				{
					id:"3",
					name:"完工结",
					isSelect:false
				},
				{
					id:"1",
					name:"日结",
					isSelect:false
				},
				{
					id:"2",
					name:"周结",
					isSelect:false
				},
				// {
				// 	id:"3",
				// 	name:"月结",
				// 	isSelect:false
				// }
			],
		}
	},
	methods: {
		next(e){
			if(this.sunit == ''){
				uni.showToast({
					icon:"none",
					title:"请选择工资单位"
				})
				return
			}
			if(this.money == ''){
				uni.showToast({
					icon:"none",
					title:"请选择基本工资"
				})
				return
			}
			if(this.speriod == ''){
				uni.showToast({
					icon:"none",
					title:"请选择结算周期"
				})
				return
			}
			var _this = this
			uni.getStorage({
			    key: 'jobData',
			    success: function (res) {
			        console.log(res.data);
					_this.jobData = res.data
					_this.jobData.reward = _this.money + '元/' + _this.speriodName
					_this.jobData.settlement = parseInt(_this.speriod)
					_this.jobData.swelfare = _this.swelfare
					console.log(_this.jobData);
					uni.setStorage({
					    key: 'jobData',
					    data: _this.jobData,
					    success: function () {　　　　　　　　　
					        // 存储成功后跳转第三步
					        uni.navigateTo({
					        	 url:"job-release-four"
					        })
					    }
					}) 
			    }
			});
			
		},
		getMoney(e){
			this.money = e.detail.value;
		},
		welfareClick(index, name){
			this.welfares[index].isSelect = !this.welfares[index].isSelect
			//选中赋值
			if(this.welfares[index].isSelect){
				if(index==0){
					this.swelfare.isBagEating = 1
				}
				if(index==1){
					this.swelfare.encase = 1
				}
				if(index==2){
					this.swelfare.isTrafficSubsidy = 1
				}
				if(index==3){
					this.swelfare.royalty = 1
				}
				if(index==4){
					this.swelfare.idFreeEdu = 1
				}
			}
			//取消选中赋值
			if(!this.welfares[index].isSelect){
				if(index==0){
					this.swelfare.isBagEating = 2
				}
				if(index==1){
					this.swelfare.encase = 2
				}
				if(index==2){
					this.swelfare.isTrafficSubsidy = 2
				}
				if(index==3){
					this.swelfare.royalty = 2
				}
				if(index==4){
					this.swelfare.idFreeEdu = 2
				}
			}
			// console.log(this.swelfare);
		},
		periodClick(index, name){
			if(index==this.speriod){
				this.speriod = '';
				return
			}
			this.speriod = index;
			this.speriodName = name;
		},
		itemclick(index){
			// 多选
			// var item = this.jobitems[index];
			// item.isSelect = !item.isSelect;
			// 单选
			if(index==this.sunit){
				this.sunit = '';
				return
			}
			this.sunit = index;
		},
	}
}
